import axios from "axios"
import _ from "lodash"
import moment from "moment"

const api = process.env.VUE_APP_API_URL;

export default {}

// 
export function downloadBlob({ name, ieName, url, method, data, params } = {}) {
  const options = method == "get" ? getOptions({
    method: "get",
    url,
    responseType: "blob",
    params
  }) : getOptions({
    method: "post",
    url,
    responseType: "blob",
    data
  });
  axios(options).then(res => {
    //res.datablob,application/vnd.openxmlformats-officedocument.spreadsheetml.sheetxlsx
    var blob = new Blob([res.data], {
      type:
        "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8"
    });
    var downloadElement = document.createElement("a");
    var href = window.URL.createObjectURL(blob); //
    downloadElement.href = href;
    if (navigator.msSaveBlob) {
      navigator.msSaveBlob(blob, name);
    } else {
      downloadElement.download = ieName; //
    }
    document.body.appendChild(downloadElement);
    downloadElement.click(); //
    document.body.removeChild(downloadElement); //
    window.URL.revokeObjectURL(href); //blob
  });
}

// data[File]
export function upload({ url, data }) {
  return new Promise((resolve, reject) => {
    let params = new FormData();
    _.map(data, unit => {
      params.append("files", unit);
    });
    axios({
      url: url ? url : api + "/fileUpload/carriersBusinessLicense",
      method: "post",
      headers: {
        "Content-Type": "multipart/form-data"
      },
      data: params
    }).then(res => {
      resolve({ error: false, message: "", res: res, data: res.data.data })
    }).catch(err => {
      reject({ error: true, message: "" })
    })
  })
}

// 
export function isObject(value) {
  return value === Object(value)
}

// (datearray)
export function typeOf(value) {
  let str = Object.prototype.toString.call(value)
  return str.match(/\[object (.*?)\]/)[1].toLowerCase()
}

// axios
export function getOptions(options) {
  return {
    ...options,
    url: api + options.url,
    headers: {
      "X-Requested-With": "XMLHttpRequest",
      "Content-Type": "application/Json; charset=UTF-8",
      "X-Authorization": "Bearer" + sessionStorage.getItem("token")
    }
  }
}

// 201901018
export function dealDate({ value, format } = {}) {
  let str = (value + "").slice(0, 4) +
    "-" +
    (value + "").slice(4, 6) +
    "-" +
    (value + "").slice(6)
  return format ? moment(str).format(format) : str
}